//Laptop L 1200px
$screen-xl: 1200px;
@mixin xl {
  @media (max-width: #{$screen-xl}) {
    @content;
  }
}
//Laptop L end

$screen-lg: 1055px;
@mixin lg1055 {
  @media (max-width: #{$screen-lg}) {
    @content;
  }
}

$mobile-xl: 500px;
@mixin mxl {
  @media (max-width: #{$mobile-xl}) {
    @content;
  }
}

$mobile-m: 425px;
@mixin mm {
  @media (max-width: #{$mobile-m}) {
    @content;
  }
}
